* {
  margin: 0;
  padding: 0; }

body {
  background-color: #fafafa;

  .bg-faded {
    background-color: white; }

  .navbar {
    border-bottom: 3px solid rgb(0, 170, 255);
    margin-bottom: 1.3rem;

    .navbar-brand:before {
      content: '#';
      color: #3897f0; }

    @media (max-width: 991px) {
      .container {
        padding-right: 0;
        padding-left: 0; } }

    @media (max-width: 576px) {
      .container {
        position: initial;
        margin-left: 0; } } }

  .container {
    article {
      .blockquote {
        border-left: 2px solid #28a745;
        padding-left: 1rem; }

      ol {
        margin-left: 1rem;
        margin-right: 1rem; } }

    .card + .card {
      margin-top: 1rem; } }

  #search {
    margin-bottom: 0; }

  [data-name="card"] {
    margin-bottom: 1rem;
    .btn-block {
      margin-top: .5rem;
      white-space: inherit;
      transition: background-color .4s ease-out; }
    .nav.flex-column {
      width: 100%;
      .nav-link {
        padding-left: 1rem;
        padding-right: 1rem;
        padding-top: .05rem;
        padding-bottom: .1rem; } } }

  [data-append] {
    blockquote, iframe, .instagram-media {
      max-width: 100% !important; } }

  [data-loader] {
    margin-top: 1rem;
    margin-bottom: 1rem; } }



